import React, { useEffect, useContext } from "react";
import { connect } from "react-redux";
import { withRouter } from "react-router-dom";
import { Container, Row } from "reactstrap";
import { getSelectedItemIds } from "state/BaseSelector";
import { MYMarkSelected } from "state/Listing/Actions";
import { get, getMyList, removeSelected } from "state/Listing/Epic";
import DashboardMainMenu from "../../components/DashboardMainMenu";
import BaseIndexPage from "../../components/HOC/BaseIndexPage";
import { mySelectList } from "state/Listing/Selectors";
import Listing from "./Components/Listing";
import VerticalMenu from "./Components/VerticalMenu";
import { AuthContext } from "Boot/auth/AuthProvider";
import { isNull } from "lodash";

const Index = ({ getIndex, list, ...props }) => {
  const { user } = useContext(AuthContext);

  let userId = null;
  if(!isNull(user)){
    userId = user._id;
  }

  useEffect(() => {
    getIndex({userId});
  }, []);
  return (
    <div className=" mg-top-90 ">
      <DashboardMainMenu />
      <Container>
        <Row className="py-4">
          <VerticalMenu />
          <Listing
            list={list}
            title="My Property Listings"
            description="This is a list of properties that you've previously advertised with
          us, or that are ready to be published."
          subTitle="All published/unpublished"
            {...props}
          />
        </Row>
      </Container>
    </div>
  );
};

const mapStateToProps = (state) => {
  return {
    noHeader: true,
    noBanner: true,
    list: mySelectList(state),
    selectedIds: getSelectedItemIds(mySelectList(state)),
  };
};

const mapDispatchToProps = (dispatch) => ({
  get: (params) => dispatch(get(params)),
  remove: (params) => dispatch(removeSelected(params)),
  getList: (params) => dispatch(getMyList(params)),
  markSelected: (item) => dispatch(MYMarkSelected(item)),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withRouter(BaseIndexPage(Index)));
