import size from "lodash/size";
import map from "lodash/map";
import React from "react";
import { Col } from "reactstrap";
import FlatList from "./FlatList";
import LoadingContent from "components/global-components/Components/SkeletonLoader/MyListingLoader";
import NoDataFound from "components/global-components/Components/NoData";

const Listing = ({ list, isLoading,title, subTitle, description, ...props }) => {
  return (
    <Col md="9">
      <div className=" dashboardContent">
        <h1 className="mb-3">{title}</h1>
        <h2 className="sub-title mb-3">{subTitle}</h2>
        <p>
          {description}
        </p>
        {isLoading === true ? (
          <div>
            <LoadingContent />
            <LoadingContent />
            <LoadingContent />
          </div>
        ) : size(list) > 0 ? (
          map(list, (item) => <FlatList item={item} {...props} />)
        ) : (
          <NoDataFound />
        )}
      </div>
    </Col>
  );
};

export default Listing;
