import { AuthContext } from "Boot/auth/AuthProvider";
import isEmpty from "lodash/isEmpty";
import React, { useContext } from "react";
import { withRouter } from "react-router-dom";
import { Button, Card, CardBody, CardTitle, Col, Row } from "reactstrap";
import { PROPERTY } from "components/Routes/Constants";

const FlatList = ({
  item,
  isSubmitting,
  onEditItem,
  history,
  onDelete,
  ...props
}) => {
  const { user } = useContext(AuthContext);
  const {
    image_attributes,
    property_address,
    created_by,
    is_published,
    display_id,
    _id,
  } = item;

  return (
    <Card>
      <CardTitle>
        <CardBody>
          <div className="listing-card">
            <Row>
              <Col
                md="3"
                className="align-items-center col-md-3 d-flex justify-content-center"
              >
                {!isEmpty(image_attributes) ? (
                  <img src={image_attributes[0].location} alt="List" />
                ) : (
                  <p>
                    <strong>No Image found</strong>
                  </p>
                )}
              </Col>
              <Col md="9" className="text-left">
                <h3>
                  {property_address}
                  <i className="fa fa-external-link"></i>
                </h3>
                <p className=" status border-right pr-3">
                  ID: <span>{display_id}</span>
                </p>
                <p className="status">
                  Status :
                  <span className="mx-1">
                    {is_published
                      ? "Published"
                      : "Listing hasn't been published"}
                  </span>
                </p>
                <hr />
                <Row className=" align-items-center">
                  <Col md="4" xs="6">
                    <Button
                      className=" btn-small btn-blue btn "
                      style={{ height: "39px", lineHeight: "30px" }}
                      onClick={() => {
                        if (user._id === created_by) {
                          history.push(`${PROPERTY}/${display_id}/#step1`);
                        } else {
                          history.push(`/listing/${display_id}`);
                        }
                      }}
                    >
                      View Listing
                    </Button>
                  </Col>
                  <Col md="3" xs="4">
                    <button className="trash-button">
                      <i
                        className="text-danger far fa-trash-alt"
                        onClick={() => onDelete({ item })}
                      ></i>
                    </button>
                  </Col>
                </Row>
              </Col>
            </Row>
          </div>
        </CardBody>
      </CardTitle>
    </Card>
  );
};

export default withRouter(FlatList);
