import UserCard from "components/section-components/UserCard/UserCard";
import React from "react";
import { withRouter } from "react-router";
import { NavLink } from "react-router-dom";
import { Col } from "reactstrap";

const VerticalMenu = (props) => {
  const {
    location: { pathname },
  } = props;
  const activeclass = {
    color: "rgb(255, 255, 255)",
    transition: "all 0.5s ease",
    backgroundColor: "#40586d",
  };
  return (
    <Col md="3">
      <div className="userDetailsMenu userDetailsMenuDesktop">
        <ul className="vertical-menu__list">
          <UserCard />

          <li>
            <NavLink
              activeStyle={pathname === "/my-listing" && activeclass}
              to="/my-listing"
              target=""
            >
              My Listing
            </NavLink>
          </li>
        </ul>
      </div>
    </Col>
  );
};

export default withRouter(VerticalMenu);
